.app {
  text-align: center;
  padding: 30px;
}
@media screen and (max-width: 1024px) {
  .app {
    padding: 30px 20px;
  }
}
@media screen and (max-width: 900px) {
  .app {
    padding: 30px 15px;
  }
}
@media screen and (max-width: 768px) {
  .app {
    padding: 30px 10px;
  }
}
