.Pokedeck {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.Pokedeck-cards {
  display: flex;
  width: 100%;
  justify-content: space-between;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.Pokedeck-winner {
  color: #46c559;
}

.Pokedeck-loser {
  color: #ff6347;
}
